import { useNavigate } from 'react-router'
import BackPNG from '@/assets/mobile/back.png'
import './index.css'

const NavMobile = ({ title, component }) => {
  const navigate = useNavigate()

  return (
    <div className="nav-mobile">
      <div
        className="nav-mobile-back"
        onClick={() => {
          navigate(-1)
        }}
      >
        <img src={BackPNG} alt="" />
      </div>
      <div className="nav-mobile-title">{title}</div>
      {component}
    </div>
  )
}

export default NavMobile
