import { useContext } from 'react'
import { AppContext } from '../..'
import TagMobile from '../Tag'
import LetterOption from '../LetterOption'
import './index.css'

const ProblemMobile = ({ data }) => {
  const { curIndex, problems, setProblems } = useContext(AppContext)

  const handleClick = (index) => {
    if (data.type === 1 || data.type === 3) {
      // 单选 判断
      const newProblems = [...problems]
      const newOptions = data?.options?.map((item, ind) => {
        return {
          ...item,
          selected: ind === index ? !data?.options[index].selected : false,
        }
      })
      newProblems[curIndex].options = newOptions
      setProblems([...newProblems])
    } else if (data.type === 2) {
      // 多选
      const newProblems = [...problems]
      newProblems[curIndex].options[index].selected =
        !newProblems[curIndex].options[index].selected
      setProblems([...newProblems])
    }
  }

  return (
    <div className="problem-mobile">
      <TagMobile type={data.type} />
      <span className="problem-mobile-title">{data.title}</span>
      <div className="problem-mobile-options">
        {data?.options?.map((item, index) => (
          <div
            className="problem-mobile-options-item"
            key={item.option}
            onClick={() => handleClick(index)}
          >
            <LetterOption flag={item.option} check={item.selected} />
            <span className="problem-mobile-options-item-desc">
              {item.content}
            </span>
          </div>
        ))}
      </div>
    </div>
  )
}

export default ProblemMobile
