import { useState, useEffect, useContext } from 'react'
import classNames from 'classnames'
import { AppContext } from '../..'
import { Portal } from 'react-portal'
import './index.css'

const ProblemPopup = ({ changePopupState }) => {
  const { curIndex, setCurIndex, problems } = useContext(AppContext)
  const [answered, setAnswered] = useState(0) // 已回答
  const [notAnswered, setNotAnswered] = useState(0) // 未回答

  const handleClick = (index) => {
    setCurIndex(index)
    changePopupState()
  }

  const handleChangePopup = (e) => {
    changePopupState()
  }

  const checkFunc = (index) => {
    const state = problems[index]?.options?.some((item) => {
      return item.selected
    })
    return state
  }

  useEffect(() => {
    let a = 0
    let na = 0
    problems.forEach((item) => {
      const state = item.options.some((items) => {
        return items.selected
      })
      if (state) a += 1
      else na += 1
    })
    setAnswered(a)
    setNotAnswered(na)
  }, [problems])

  return (
    <Portal>
      <div className="problem-popup" onClick={handleChangePopup}>
        <div
          className="problem-popup-content"
          onClick={(e) => {
            e.stopPropagation()
          }}
        >
          <div className="ppc-header-info">
            <div className="ppchi-box">
              <span>已答题</span>
              <span>{answered}</span>
            </div>
            <div className="ppchi-box">
              <span>未答题</span>
              <span style={{ color: '#999' }}>{notAnswered}</span>
            </div>
          </div>
          <div className="ppc-content-box">
            {problems.map((item, index) => {
              const itemClass = classNames('ppc-content-box-item', {
                'problem-popup-item-border': curIndex === index,
                'problem-popup-item-selected': checkFunc(index),
              })
              return (
                <div
                  key={index}
                  className={itemClass}
                  onClick={() => handleClick(index)}
                >
                  {index + 1}
                </div>
              )
            })}
            <div className="ppcc-f" />
          </div>
        </div>
      </div>
    </Portal>
  )
}

export default ProblemPopup
