// 根据传入的秒，格式化成 分：秒
export const formatSecond = (second) => {
  if (second <= 0) return `00:00`
  const minute = Math.floor(second / (60 * 1000))
  const sec = (second - minute * 60 * 1000) / 1000
  const stringMinute = `00${minute}`.slice(-2)
  const stringSec = `00${sec}`.slice(-2)
  return `${stringMinute}:${stringSec}`
}
