import CheckedPNG from '@/assets/mobile/checked.png'
import './index.css'

const LetterOption = ({ check = false, flag = '' }) => {
  return (
    <>
      {check ? (
        <span className="letter-option-mobile-checked-box">
          <img
            className="letter-option-mobile-checked"
            src={CheckedPNG}
            alt=""
          />
        </span>
      ) : (
        flag && <span className="letter-option-mobile">{flag}</span>
      )}
    </>
  )
}

export default LetterOption
