import { useContext, useState, useEffect } from 'react'
import { AppContext } from '../..'
import { formatSecond } from '@/utils'
import ProblemPopup from '../ProblemPopup'
import PrePNG from '@/assets/mobile/pre.png'
import NextPNG from '@/assets/mobile/next.png'
import ProblemsNG from '@/assets/mobile/problems.png'
import ClockPNG from '@/assets/mobile/clock.png'
import './index.css'

const Timer = ({ timerEnd }) => {
  const [timer, setTimer] = useState(5 * 60 * 1000)
  // const [timer, setTimer] = useState(6 * 1000)

  useEffect(() => {
    let t = null
    if (timer > 0) {
      t = setTimeout(() => {
        setTimer(timer - 1000)
      }, 1000)
    } else {
      clearTimeout(t)
      timerEnd()
    }

    return () => {
      clearTimeout(t)
    }
  }, [timer, timerEnd])

  return (
    <li>
      <img src={ClockPNG} className="exam-nav-mobile-icon-spe" alt="" />
      <span>{formatSecond(timer)}</span>
    </li>
  )
}

const ExamNavMobile = ({ submit }) => {
  const { curIndex, setCurIndex, problemsLength } = useContext(AppContext)
  const [show, setShow] = useState(false)

  const handlePreClick = () => {
    if (curIndex > 0) setCurIndex(curIndex - 1)
  }

  const handleNextClick = () => {
    if (curIndex < problemsLength - 1) setCurIndex(curIndex + 1)
  }

  const changePopupState = () => {
    setShow(!show)
  }

  // 倒计时结束后执行
  const timerEnd = () => {
    submit()
  }

  return (
    <div className="exam-nav-mobile">
      <ul>
        <li onClick={handlePreClick}>
          <img src={PrePNG} className="exam-nav-mobile-icon" alt="" />
          <span>上一题</span>
        </li>
        <li onClick={handleNextClick}>
          <img src={NextPNG} className="exam-nav-mobile-icon" alt="" />
          <span>下一题</span>
        </li>
        <li onClick={changePopupState}>
          <img src={ProblemsNG} className="exam-nav-mobile-icon-spe" alt="" />
          <span>
            {curIndex + 1}/{problemsLength}
          </span>
        </li>
        <Timer timerEnd={timerEnd} />
      </ul>
      {show && <ProblemPopup changePopupState={changePopupState} />}
    </div>
  )
}

export default ExamNavMobile
