import { useState, useMemo, createContext } from 'react'
import { useNavigate } from 'react-router'
import { Button } from 'antd-mobile'
import NavMobile from '../../../components/mobile/Nav'
import ProblemMobile from './components/Problem'
import ExamNavMobile from './components/ExamNav'
import './index.css'

export const AppContext = createContext({})

const ExamMobile = () => {
  const mockData = useMemo(() => {
    return [
      {
        id: 1,
        title: '未煮熟四季豆、新鲜黄花菜、发芽土豆不能食用的原因是（ ）',
        type: 1, // 1-单选 2-多选 3-判断
        options: [
          {
            option: 'A',
            content: '腐败变质',
            selected: false,
          },
          {
            option: 'B',
            content: '含有致病菌',
            selected: false,
          },
          {
            option: 'C',
            content: '含有生物毒素',
            selected: false,
          },
        ],
      },
      {
        id: 2,
        title: '厨师的个人卫生要求中描述错误的是（  ）',
        type: 1, // 1-单选 2-多选 3-判断
        options: [
          {
            option: 'A',
            content: '从业人员上厕所前应在处理区内脱去工作服',
            selected: false,
          },
          {
            option: 'B',
            content: '操作时，禁止吃东西，随地吐痰，不准面对食品咳嗽，打喷嚏',
            selected: false,
          },
          {
            option: 'C',
            content: '可以适当时候吸烟',
            selected: false,
          },
          {
            option: 'D',
            content:
              '穿戴整洁，勤换衣服，勤理发，勤洗澡，不得留长指甲和染指甲油，上班时候不得戴戒指，项链等首饰物品',
            selected: false,
          },
        ],
      },
      {
        id: 3,
        title:
          '餐饮服务提供者在制作食品时使用药食两用物质的， 应遵守下列（ ）规定。',
        type: 2, // 1-单选 2-多选 3-判断
        options: [
          {
            option: 'A',
            content: '不得添加任何西药',
            selected: false,
          },
          {
            option: 'B',
            content: '不得添加任何中草药',
            selected: false,
          },
          {
            option: 'C',
            content: '不得添加药品，但可以添加按照传统既是食品又是中药材的物质',
            selected: false,
          },
          {
            option: 'D',
            content: '不得标识、宣传制作的食品具有疾病预防或治疗功能品',
            selected: false,
          },
        ],
      },
      {
        id: 4,
        title:
          '制定食品安全标准，应当以保障公众身体健康为宗旨，做到科学合理、安全可靠。（      ）',
        type: 3, // 1-单选 2-多选 3-判断
        options: [
          {
            option: 'A',
            content: '正确',
            selected: false,
          },
          {
            option: 'B',
            content: '错误',
            selected: false,
          },
        ],
      },
    ]
  }, [])
  const navigate = useNavigate()
  const [curIndex, setCurIndex] = useState(0)
  const [problems, setProblems] = useState([...mockData])

  const initialState = {
    curIndex,
    setCurIndex,
    problems,
    setProblems,
    problemsLength: problems.length,
  }

  const submit = () => {
    alert('考试结束，已自动交卷！')
    navigate('/', { replace: true })
  }

  const rightComponent = () => {
    return (
      <Button
        color="primary"
        size="small"
        style={{ marginRight: 10 }}
        onClick={submit}
      >
        交卷
      </Button>
    )
  }

  return (
    <AppContext.Provider value={{ ...initialState }}>
      <div className="exam-mobile">
        <NavMobile title="考试" component={rightComponent()} />
        {problems?.map((item, index) =>
          index === curIndex ? <ProblemMobile data={item} key={item.id} /> : ''
        )}
        <ExamNavMobile submit={submit} />
      </div>
    </AppContext.Provider>
  )
}

export default ExamMobile
